.PcNewAccount-container{
    display: flex;
    width: 100%;
    height: calc(100vh - 190px);
    justify-content: center;
    align-items: center;

}

.PcNew-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 350px;
    height: 400px;
    background: #f5f5f5;

}
.PcNew-items-container{
    display: flex;
    justify-content: space-around;
    width: 200px;
    height: 250px;

    flex-direction: column;
    align-items: center;
    align-content: space-between;
    flex-wrap: nowrap;
}
.PcNew-items-container input{
    width: 300px;
    height: 25px;
}


