.footer-nav {

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 10px 20px;
    background-color: #f5f5f5;
    box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.1);
    bottom: 0;
    left: 0;
    right: 0;
}

.logos {
    width: 784px;
    display: flex;
    gap: 10px;
    margin-top: 10px;
    justify-content: space-between;
}

.nav-logo {
    flex-shrink: 0;
}

.logo-img {
    width: 50px;
    height: auto;
}

.nav-links {
    width: 784px;
    display: flex;
    gap: 20px;
    flex-grow: 1;
    justify-content: space-evenly;
}

.nav-link {

    text-decoration: none;
    color: #333;
    padding: 5px 10px;
    transition: background-color 0.3s;
    border-radius: 4px;
}

.nav-link-vertical-line{
    height: 28px;
    border-left: 1px solid #A4A4A4;
}



.nav-link:hover {
    background-color: #f0f0f0;
}


.nav-logo img{
    width: 160px;
    height: 47px;
}

.nav-logo-right{
    padding-top: 18px;
    color: #626264;
}