.category-container-select{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 30px;
}

.category-container2-select{
    text-align: right;
    width: 1120px;
    height: 30px;
}



.category-container-page{
    display: flex;
    justify-content: center;
}


.selected-page {
    background-color: grey;
    border-radius: 50%;
    color: white;
    padding: 5px 10px;
}

.CategoryNav{
    height: 40px;
    width: 100%;
    background-color: #F7F7F7;
    display: flex;
    justify-content: center;
    align-items: center;

}
.CategoryNavText{
    height: 40px;
    width: 1120px;
    line-height: 40px;
}

.All{
    height: 168px;
    width: 100%;
    background-color: #fff;
    font-size: 32px;
    line-height: 168px;
    text-align: center;
}

.CategoryFooterNav{
    height: 232px;
    width: 100%;
    background-color: #fff;
}
