.product-detail-container{
    width: 100%;
    display: flex;
    justify-content: center;
}

.product-detail {
    display: flex;
    width: 1140px;
    padding: 20px;

    margin: 20px;

}

.left-section {
    flex: 2;
    margin-right: 20px;
}

.main-image {
    width: 100%;
    max-width: 400px;
    margin-bottom: 10px;
}

.sub-images {
    display: flex;
    gap: 10px;
}

.sub-image {
    width: 160px;
    height: 160px;
    object-fit: cover;
}

.right-section {
    flex: 1;
    padding-left: 20px;

}

.price {
    color: #1A1A1C;
    font-size: 16px;
}

.stock {
    margin-top: 10px;
    color: #1A1A1C;
}

.description {
    margin-top: 20px;
    color: #626264;
    white-space: pre-wrap;
}


.product-detail-cartBtn{
    display: flex;
    justify-content: center;
    align-items: center;
    color: #F4F4F4;
    background-color: #F57948;
    width: 448px;
    height: 56px;
    font-size: 16px;
    cursor: pointer;
}

.product-detail-cartBtn svg{
    width: 24px;

}
.product-detail-cartBtn span{
    width: 116px;
    height: 25px;
    line-height: 25px;
}


a{
    text-decoration: none;
}

.product-detail-LoveBtn{

    margin-top: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #F4F4F4;
    background-color: #69B5C4;
    width: 448px;
    height: 56px;
    font-size: 16px;
    cursor: pointer;
}
.product-detail-LoveBtn svg{
    width: 24px;
}

.product-detail-LoveBtn span{
    width: 116px;
    height: 25px;
    line-height: 25px;
}





.PcShopHistory-items-container{
    width: 827px;
    height: 217px;
    border-bottom: 1px solid #A4A4A4;

}