.MyPageNav{
    height: 40px;
    width: 100%;
    background-color: #F7F7F7;
    display: flex;
    justify-content: center;
    align-items: center;

}
.MyPageText{
    height: 40px;
    width: 1120px;
    line-height: 40px;
}

.TEXTMyPage{
    height: 168px;
    width: 100%;
    background-color: #fff;
    font-size: 32px;
    line-height: 168px;
    text-align: center;
}




.menu-container {
    width: 100%;
    height: 400px;
    display: flex;
    justify-content: center;

}

.menu-container1 {
    width: 1120px;
    height: 300px;
    background-color: #F4F4F4;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px; /* 间距 */
    padding-left: 16px; /* 内左边距 */
}
.menu-container2{
    display: flex;
    width: 928px;
    justify-content: center;
    flex-direction: column;
}

.menu-container::before {
    content: "";
    flex: 1;
}
.menu-container::after {
    content: "";
    flex: 1;
}

.NavTop a{
    text-decoration: none;
    color: #1A1A1C;
}


.NavTop{
    width: 828px;
    height: 40px;
    margin-left: 100px;
    padding-left: 100px;
    border-bottom:1px solid #A4A4A4;
    line-height: 40px;
    color: #1A1A1C;
}

.NavTop:first-child{
    border-top:1px solid #626264;
}