.carousel-container {
    position: relative;
    width: 1120px;
    height: 416px;
    overflow: hidden;
    margin: 0 auto;
}

.carousel-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0;
    transition: opacity 0.5s;
}

.carousel-image.active {
    opacity: 1;
}

.carousel-buttons {
    bottom: 30px;
    left: 50%;
    display: flex;
    gap: 10px;
}

.carousel-btn {
    width: 24px;
    height: 4px;
    background-color: #A4A4A4;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}

.carousel-btn.active {
    background-color: #333;
}

.carousel-btn:hover {
    background-color: rgba(0, 0, 0, 0.7);
}


/*留白*/
.spacer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 128px;
}