/* styles.css */







.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 256px);
    gap: 32px;
    justify-content: center;
}

.grid-item {
    width: 256px;
    min-height: 360px;

    overflow: hidden;
    text-decoration: none;
    /*position: relative;    注意*/
}

.grid-item img {
    width: 256px;
    height: 256px;
    object-fit: cover;
}
.product-info {
    padding-top: 8px;
}

.product-name {
    font-weight: bold;
    font-size: 16px;
    color: #626264;
}

.product-price {
    color: #1A1A1C;

}

.like-icon {
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.7);
    border-radius: 50%;
    padding: 5px;
    z-index: 10;  /* 确保它在图片上方 */
}


.LinkBtnAnimaPCCategory{
    cursor: pointer;
    transition: background-color 0.3s ease;
    opacity: 1;
    pointer-events: all;
}

.LinkBtnAnimaPCCategory:disabled {
    opacity: 0.5;
    pointer-events: none;
}

.LinkBtnAnimaPCCategory:hover {
    background-color: #ddd;
}