
.PcCartNext-container-outer {
    display: flex;
    width: 100%;
    justify-content: center;
    height: 1000px;
}

.PcCartNext-container {
    display: flex;
    width: 1120px;
}

.PcCartNext-container-left {
    flex: 2;
    height: 200px;

}

.PcCartNext-container-right {
    flex: 1;
    height: 200px;

}
.PcCartNext-container-PAYMENT-Nav{
    height: 92px;
    font-size: 32px;
    display: flex;
    border-bottom: 1px solid #A4A4A4;
    justify-content: center;
    align-items: center;
}

.PcCartNext-PAYMENT-Address-confirm{

    border-bottom: 1px solid #A4A4A4;
    flex-direction: column;

}
.PcCartNext-PAYMENT-Address-confirm-left{

    width: 740px;
    height: 116px;
    margin-top: 16px;
    font-size: 16px;
    font-family: Noto Sans JP;
    /*background-color: red;*/
    display: flex;
}
.PcCartNext-PAYMENT-Address-confirm-right{
    border-top: 1px solid #A4A4A4;
    margin-top: 16px;
    font-size: 20px;
    font-family: Noto Sans JP;
    /*background-color: green;*/
}

.PcCartNext-PAYMENT-Address-confirm-left-left{
    flex: 1;
}


.PcCartNext-PAYMENT-Address-confirm-left-middle{
    flex: 3;
}

.PcCartNext-PAYMENT-Address-confirm-left-right{
    flex: 1;
}

.PcCartNext-container-accounts{
    margin-top: 90px;
    width: 352px;
    height: 432px;
    margin-left: 10px;
    background-color: #F4F4F4;
    display: flex;
    flex-direction: column;
    align-content: flex-end;
    justify-content: space-evenly;
    align-items: center;
}
.PcCartNext-container-accounts-div{
    width: 90%;
    display: flex;
    justify-content: space-between;
}


/*商品列表*/
.productMap{
    display: flex;

}


.productMapLeft{

}
.productMapLeft img{
    width: 160px;
    height: 160px;
}

.productMapRight{
    font-size: 16px;
    color: #1A1A1C;
}