.navbar-container{
    width: 100%;
    display: flex;
    justify-content: center;
}
.navbar {
    width: 1120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #ffffff;
}

button {
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
}
svg{
    width: 36px;
    height: 36px;
}
.logo {
    font-size: 24px; /* 调整为合适的大小 */
    font-weight: bold;
    height: 55px;
    /* 如果使用图片作为Logo, 可能需要调整宽度和高度 */
}

.logoImg{
    margin-left: 290px;
    height: 47px;
}

.icons {
    display: flex;
    align-items: center; /* 垂直居中SVG图标 */
    gap: 10px; /* 图标之间的间距 */
}


.link{
    text-decoration: none;
    color: #333;
    padding: 5px 10px;
    transition: background-color 0.3s;
}

.link:hover {
    background-color: #f0f0f0;
}





.nav-bar-search{
    width: 300px;
    height: 40px;
}



.nav-bar-search-icon{
    position: relative;
    width: 20px;
    height: 20px;
    right: -30px;
    top: 5px;

}




.navbar-container-menu{
    display: flex;
    width: 100%;
    height: 150px;
    position: absolute;
    justify-content: center;
    top:75px;
}
.navbar-container-mid-menu{
    height: 150px;
    width: 1120px;
    z-index: 2;
}

.menu {
    width: 240px;
    background-color: white;
    padding: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    z-index: 3; /* 确保菜单显示在其他内容上方 */
}

.LinkCartBtnAnima{
    cursor: pointer;
    transition: background-color 0.3s ease;
    opacity: 1;
    pointer-events: all;
}

.LinkCartBtnAnima:disabled {
    opacity: 0.5;
    pointer-events: none;
}

.LinkCartBtnAnima:hover {
    background-color: #ddd;
}
