


.PcShopHistoryNav{
    height: 40px;
    width: 100%;
    background-color: #F7F7F7;
    display: flex;
    justify-content: center;
    align-items: center;
}


.PcShopHistoryText{
    height: 40px;
    width: 1120px;
    line-height: 40px;

}



.TEXTShopHistory{
    height: 168px;
    width: 100%;
    background-color: #fff;
    font-size: 32px;
    line-height: 168px;
    text-align: center;
}


.PcShopHistory-container-outer{
    display: flex;
    justify-content: center;
    min-height: calc(100vh - 400px);
}


.PcShopHistory-container{
    width: 1200px;
    display: flex;
}
.PcShopHistory-left{
    flex: 0.8;
}
.PcShopHistory-right{
    flex: 2;
}

/*上方购入履历入力*/
.PcShopHistory-top-context{
    background-color: #F4F4F4;
    color: #1A1A1C;
    width: 832px;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
}



.PcShopHistory-context-container{
    width: 827px;
    height: 217px;
}


.payment-history-container {
    max-height: 833px;
    overflow-y: auto;
}

.payment-history-item {
    height: 40px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #eee;
}

.payment-history-item > div,
.payment-history-item > button {
    flex: 1;
    padding: 0 10px;
}




.PcShopHistory-items-container{
    display: flex;
    width: 827px;
    height: 217px;
    border-bottom: 1px solid #A4A4A4;
    align-items: center;
}

.PcShopHistory-items-container-left{
    flex: 1;
    width: 160px;
    height: 184px;
}

.PcShopHistory-items-container-left img{
    width: 160px;
    height: 184px;
    object-fit: cover;
}

.PcShopHistory-items-container-right{
    flex: 3;
}