

.PcHotShopNav{
    height: 40px;
    width: 100%;
    background-color: #F7F7F7;
    display: flex;
    justify-content: center;
    align-items: center;

}
.PcHotShopNavText{
    height: 40px;
     width: 1120px;
    line-height: 40px;
}

.All{
    height: 168px;
    width: 100%;
    background-color: #fff;
    font-size: 32px;
    line-height: 168px;
    text-align: center;
}

.PcHotShopFooterNav{
    height: 232px;
    width: 100%;
    background-color: #fff;
}




.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 256px);
    gap: 32px;
    justify-content: center;
}

.grid-item {
    width: 256px;
    min-height: 360px;

    overflow: hidden;
    text-decoration: none;

}

.grid-item img {
    width: 256px;
    height: 256px;
    object-fit: cover;
}
.product-info {
    padding-top: 8px;
}

.product-name {
    font-weight: bold;
    color: #626264;
}

.product-price {
    color: #626264;

}