}
.viewMore-container .viewMore{
    content: '';
    border: 1px solid #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 256px;
    height: 56px;
    border-radius: 30px;
    font-size: 16px;

}

.viewMore:before{
    content: '';
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);  /* 使三角形垂直居中 */
    width: 12px;  /* 调整为所需的宽度 */
    height: 12px;  /* 调整为所需的高度 */
    margin-left: 53px;
}

.viewMore:after{
    content: '';
    -webkit-transform:rotate(45deg);
            transform:rotate(45deg);  /* 使三角形垂直居中 */
    width: 9px;  /* 调整为所需的宽度 */
    height: 9px;  /* 调整为所需的高度 */
    border-top: 1px solid black;  /* 上面的线段 */
    border-right: 1px solid black;  /* 右侧的线段 */
    margin-left: 50px;
}

