
.PcPayPage-container-out{
    width: 100%;
    height: calc(100vh - 190px);
    display: flex;
    justify-content: center;
}


.PcPayPage-container{
    display: flex;
    width: 1160px;
    height: 100%;
}

.PcPayPage-container-left{
    flex: 2;

    height: 100%;
}

.PcPayPage-container-right{
    flex: 1;

    height: 100%;
}

.PcPayPage-container-left-left{
    height: 100%;
    width: 640px;

}

.PcPayPage-container-left-left-Text{
    display: flex;
    height: 90px;
    font-size: 32px;
    align-items: center;
    justify-content: center;

    border-bottom: 1px solid #A4A4A4;
}
.PcPayPage-container-left-left-button{
    width: 100%;
    height: 200px;
    display: flex;

}


.PcPayPage-container-left-left-button-left{
    width: 300px;
    height: 100px;

}

.PcPayPage-container-left-left-button-right{
    width: 300px;
    height: 100px;

}



.wechatPay svg{
    width: 120px;
}


.creditCard svg{
    width: 200px;
    height: 53px;
}




.labelWechatPay{
    display: flex;
}

.labelCreditCard{
    display: flex;
    align-items: flex-start;
}

.PcPayPage-container-right-box{
    display: flex;
    width: 350px;
    height: 320px;
    border: 1px solid #A4A4A4;
    align-items: center;
    justify-content: center;
    background-color: #F4F4F4;
}

.PcPayPage-container-right-box-inline{
    display: flex;
    width: 320px;
    height: 290px;
    flex-direction: column;
    justify-content: space-evenly;
}
.PcPayPage-btm:hover{
    border: 1px solid black;
}