    text-align: center;
}
.PcPostFooterNav{
    height: 232px;
    width: 100%;
    background-color: #fff;
}
.grid-container-favorite {
    display: grid;
    grid-template-columns: repeat(3, 256px);
    gap: 32px;
    justify-content: center;
}
.grid-item {
    width: 256px;
    min-height: 360px;
    overflow: hidden;
    text-decoration: none;
}
.grid-item img {
    width: 256px;
    height: 256px;
    object-fit: cover;
}
.product-info {
    padding-top: 8px;
}

.product-name {
    font-weight: bold;
    color: #626264;
}

.product-price {
    color: #626264;

}



