.PCAccountManagement-container{
    height: calc(100vh - 190px);
    width: 100%;
    display: flex;
    justify-content: center;
}

.PCAccountManagement-container-center{
    height: calc(100vh - 190px);
    width: 1120px;
    display: flex;
}

.PCAccountManagement-container-center-left{

    flex: 1;
}

.PCAccountManagement-container-center-right{

    flex: 2;
}

.PCAccountManagement-container-center-right-text{
    display: flex;
    font-size: 20px;
    width: 832px;
    height: 40px;
    background-color: #F4F4F4;
    align-items: center;
    padding-left: 10px;
}

.PCAccountManagement-container-center-right-show{
    display: flex;
    width: 100%;
    height: calc(100vh - 390px);
    justify-content: center;
    align-items: center;
}
.PCAccountManagement-container-center-right-show-inside{

    flex-direction: column;
    display: flex;
    justify-content: space-evenly;
    width: 444px;
    height: 300px;
}

.PCAccountManagement-container-center-right-show-inside input{
    width: 438px;
    height: 52px;

}

.PCAccountManagement-container-center-right-show-inside button{
    width: 448px;
    height: 56px;
}