.menu-container-nav {
    width: 100%;
    height: 296px;
    display: flex;
    justify-content: center;

}

.menu-container1-nav {
    width: 256px;
    height: 296px;
    background-color: #F4F4F4;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10px; /* 间距 */
    padding-left: 16px; /* 内左边距 */
}
.menu-container2-nav{
    display: flex;
    width: 928px;
    justify-content: center;
    flex-direction: column;
}

.menu-container-nav::before {
    content: "";
    flex: 1;
}
.menu-container-nav::after {
    content: "";
    flex: 1;
}

.NavTop-nav a{
    text-decoration: none;
    color: #1A1A1C;
}


.NavTop-nav{
    width: 224px;
    height: 40px;
    border-bottom:1px solid #A4A4A4;
    line-height: 40px;
    color: #1A1A1C;
}

.NavTop:first-child{
    border-top:1px solid #626264;
}