.PcCart-container{
    display: flex;
    justify-content: center;
    min-height: calc(100vh - 190px)
}
.PcCart-container-inside{
    display: flex;
    width: 1120px;
}

.PcCart-container-left{
    flex: 2;

}


.PcCart-container-right{
    flex: 1;

}


.PcCart-container-accounts{
    display: flex;
    width: 354px;
    height: 280px;
    margin-left: 10px;
    background-color: #F4F4F4;
    flex-direction: column;
    align-content: flex-end;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid #A4A4A4;
}

.PcCart-container-left img{
    width: 400px;
}


.buy-btn{
    width: 320px;
    height: 56px;
    background-color: #F57948;
}

.continue-btn{
    width: 320px;
    height: 56px;
    background-color: #F5CE69;
}