


.PcCreditCard-container-out{
    color: #1A1A1C;
    width: 100%;
    height: calc(100vh - 190px);
    display: flex;
    justify-content: center;
}


.PcCreditCard-container{
    display: flex;
    width: 1160px;
    height: 100%;
}

.PcCreditCard-container-left{
    flex: 2;

    height: 100%;
}

.PcCreditCard-container-right{
    flex: 1;

    height: 100%;
}

.PcCreditCard-container-left-left{
    height: 100%;
    width: 640px;


}

.PcCreditCard-container-left-left-Text{
    display: flex;
    height: 90px;
    font-size: 32px;
    align-items: center;
    justify-content: center;

    border-bottom: 1px solid #A4A4A4;
}
.PcCreditCard-container-left-left-button{
    width: 100%;
    height: calc(100vh - 280px);
    display: flex;
    flex-direction: column;
}


.PcCreditCard-container-left-left-button-text{
    display: flex;
    width: 100%;
    height: 40px;
    justify-content: space-between;
    align-items: center;
}

.PcCreditCard-container-left-left-button-text svg{
    width: 130px;
    height: 30px;
}
.inputText{
    width: 632px;
    height: 52px;
}

.PcCreditCard-bottom-input-inline{
    display: flex;
    flex-direction: column;
}

.PcCreditCard-bottom-input{
    margin-top: 50px;
    display: flex;
    justify-content: space-between;

}
.inputBottomText{
    width: 256px;
    height: 52px;
}







.wechatPay svg{
    width: 120px;
}


.creditCard svg{
    width: 200px;
    height: 53px;
}




.labelWechatPay{
    display: flex;
}

.labelCreditCard{
    display: flex;
    align-items: flex-start;
}

.PcCreditCard-container-right-box{
    display: flex;
    width: 350px;
    height: 320px;
    border: 1px solid #A4A4A4;
    align-items: center;
    justify-content: center;
    background-color: #F4F4F4;
}

.PcCreditCard-container-right-box-inline{
    display: flex;
    width: 320px;
    height: 290px;
    flex-direction: column;
    justify-content: space-evenly;
}
.PcCreditCard-btm:hover{
    border: 1px solid black;
}

.invalid-input {
    border: 1px solid red;
}