.bestseller-container{
    width: 100%;
    background-color: #F4F4F4;
    height: 480px;
    display: flex;
    flex-direction:column;
    justify-content: center;
    flex-wrap: nowrap;
    gap: 32px;
}
.bestseller-container-image{
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    gap: 32px;
}

.bestseller-container-image img{
    width: 256px;
    height: 256px;
    object-fit: cover;
    white-space: nowrap;
}


.bestseller-container-context{
    text-align: center;
    font-size: 32px;
    line-height: 110px;
    height: 80px;
}