/* styles.css */







.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 256px);
    gap: 32px;
    justify-content: center;
}

.grid-item {
    width: 256px;
    min-height: 360px;

    overflow: hidden;
    text-decoration: none;

}

.grid-item img {
    width: 256px;
    height: 256px;
    object-fit: cover;
}
.product-info {
    padding-top: 8px;
}

.product-name {
    font-weight: bold;
    color: #626264;
}

.product-price {
    color: #626264;

}