.category-container{
    width: 100%;
    height: 860px;
}

.category-container-context{
    text-align: center;
    font-size: 32px;
    line-height: 155px;
    height: 130px;
}



.viewMore-container{
    width: 100%;
    height: 150px;
    display: flex;
    justify-content: center;
    margin-top: 60px;
    align-items: center;

}
.viewMore-container .viewMore{
    content: '';
    border: 1px solid #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 256px;
    height: 56px;
    border-radius: 30px;
    font-size: 16px;

}

.viewMore:before{
    content: '';
    transform:rotate(45deg);  /* 使三角形垂直居中 */
    width: 12px;  /* 调整为所需的宽度 */
    height: 12px;  /* 调整为所需的高度 */
    margin-left: 53px;
}

.viewMore:after{
    content: '';
    transform:rotate(45deg);  /* 使三角形垂直居中 */
    width: 9px;  /* 调整为所需的宽度 */
    height: 9px;  /* 调整为所需的高度 */
    border-top: 1px solid black;  /* 上面的线段 */
    border-right: 1px solid black;  /* 右侧的线段 */
    margin-left: 50px;
}

.productShower{
    display: flex;
    align-items: flex-start;
}

.CATEGORY-button{
    width: 100%;
    display: flex;
    justify-content: center
}


.CATEGORY-button button {
    margin: 10px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    background-color: transparent;
}

.CATEGORY-button button.selected {
    border-bottom: 2px solid black;
}


.LinkBtnAnimaPCHomePageCategory{
    cursor: pointer;
    transition: background-color 0.3s ease;
    opacity: 1;
    pointer-events: all;
}

.LinkBtnAnimaPCHomePageCategory:disabled {
    opacity: 0.5;
    pointer-events: none;
}

.LinkBtnAnimaPCHomePageCategory:hover {
    background-color: #ddd;
}

a{
    text-decoration:none;
    color:#333;
}