table{
    width: 820px;
    font-family: Noto Sans JP;
    font-size: 16px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.04em;
    text-align: left;
    color: #1A1A1C;
}
thead tr:last-child {
    border-bottom: 1px solid #626264;
}
th{
    border-bottom: 1px solid #626264;
}
tbody tr:nth-last-child(3) {
    border-bottom: 1px solid #626264;
}
tbody tr.subtotal {
    border-top: 1px solid #626264;
}