.PcWechatPay-container{
    width: 100%;
    height: calc(100vh - 190px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.PcWechatPay-text{
    display: flex;
    align-items: center;
}

.PcWechatPay-container-center-QRCode{
    width: 200px;
    height: 200px;
}

.PcWechatPay-container-center{
    width: 250px;
    height: 250px;
    display: flex;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    background-color: #3eaf7c;
}