.AQNav{
    height: 40px;
    width: 100%;
    background-color: #F7F7F7;
    display: flex;
    justify-content: center;
    align-items: center;

}
.AQText{
    height: 40px;
    width: 1120px;
    line-height: 40px;
}

.TEXTAQ{
    height: 168px;
    width: 100%;
    background-color: #fff;
    font-size: 32px;
    line-height: 168px;
    text-align: center;
}

.PcAQ-container{
    width: 100%;
    height: 952px;
    display: flex;
    justify-content: center;
}

.PcAQInside{
    display: flex;
    justify-content: center;

    background-color: #F4F4F4;
    width: 1120px;
    height: 952px;
}
.PcAQInside2{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 544px;
    height: 952px;
}

.PcAQInside3{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 544px;
    height: 156px;
}

.PcAQ-btn{
    width: 256px;
    height: 56px;
    background-color: #F57948;
    color: white;
}


/*input框*/
.custom-input {
    width: 544px;
    height: 56px;
    border: 1px solid #A4A4A4;
    border-radius: 4px;
    padding: 0 10px;
}
.custom-input::placeholder {
    color: #626264;
}

.custom-input2 {
    width: 544px;
    height: 500px;
    border: 1px solid #A4A4A4;
    border-radius: 4px;
    padding: 0 10px;
}