
/* Carousel.css */
.PciTemCarousel2-carousel-container {
    display: flex;
    align-items: center;
    position: relative;
}

.PciTemCarousel2-carousel-image {
    width: 33.33%;
    max-height: 300px;
    object-fit: cover;
}

.PciTemCarousel2-images-display {
    display: flex;
    width: 100%;
    overflow: hidden;
}

.PciTemCarousel2-btn{
    cursor: pointer;
    transition: background-color 0.3s ease;
    opacity: 1;
    pointer-events: all;
}
.PciTemCarousel2-btn:disabled{
    opacity: 0.5;
    pointer-events: none;
}

.PciTemCarousel2-btn:hover{
    background-color: #ddd;
}